body {
  font-family: 'Montserrat', sans-serif;
  background-color: #F5F5F5;
  text-align: center;
  margin-top: 50px;
}
#title {
  margin: 40px;
  letter-spacing: 4px;
}

.author {
  font-size: 13px;
  margin: 20px;
  letter-spacing: 2px;
}
.author i {
  color: black;
}
.author i:hover {
  color: #ffc9ca;
}


.app {
  text-align: justify;
}



.preview-container {
  margin: 10px;
  border-radius: 5px;
}
.preview-container h3 {
  background-color: #DEB887;
  letter-spacing: 5px;
  text-align: center;
  border: 1px solid black;

}

/* textarea */
#preview {
  border: 1px solid black;
  background-color: #ffc9ca;
  padding: 10px;
}
code {
  background: white;
  padding: 1px 4px 2px 4px;
  font-size: 12px;
  font-weight: bold;
}
.editor-container {
    margin: 10px;
}
.editor-container h3 {
    background-color: #DEB887;
    letter-spacing: 5px;
    text-align: center;
    border: 1px solid black;
}

/* textarea */
#editor {
    resize: vertical;
    width: 100%;
    height: 600px;
    min-height: 400px;
    font-size: 15px;
    background-color: #ffc9ca;
    padding: 10px;
    border: 1px solid black;
}

